"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Building2, Upload, X, Plus, ArrowLeft } from "lucide-react";
import Link from "next/link";
import { Company } from "@/types/company";

const industries = [
  "Technology",
  "Healthcare",
  "Finance",
  "Manufacturing",
  "Retail",
  "Education",
  "Construction",
  "Transportation",
  "Energy",
  "Media & Entertainment",
  "Real Estate",
  "Consulting",
  "Other",
];

const employeeSizes = [
  "1-10",
  "11-50",
  "51-100",
  "101-250",
  "251-500",
  "501-1000",
  "1000+",
];

export default function EditProfilePage() {
  const [formData, setFormData] = useState<Company>({
    id: "",
    name: "",
    website: "",
    logo: "",
    services: [],
    industry: "",
    location: "",
    description: "",
    employees: "",
    founded: "",
  });

  const [newService, setNewService] = useState("");
  const [logoPreview, setLogoPreview] = useState<string | undefined>();
  const [logoFile, setLogoFile] = useState<File | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const router = useRouter();

  const handleInputChange = (field: keyof Company, value: string) => {
    setFormData((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  const handleLogoUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0] || null;
    if (!file) return;
    setLogoFile(file);
    setLogoPreview(URL.createObjectURL(file));
  };

  const addService = () => {
    const trimmed = newService.trim();
    if (trimmed && !formData.services.includes(trimmed)) {
      setFormData((prev) => ({
        ...prev,
        services: [...prev.services, trimmed],
      }));
      setNewService("");
    }
  };

  const removeService = (serviceToRemove: string) => {
    setFormData((prev) => ({
      ...prev,
      services: prev.services.filter((s) => s !== serviceToRemove),
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    const payload = new FormData();
    // append JSON fields
    payload.append("name", formData.name);
    payload.append("industry", formData.industry);
    payload.append("location", formData.location);
    payload.append("description", formData.description);
    payload.append("website", formData.website);
    payload.append("employees", formData.employees);
    payload.append("founded", formData.founded);
    payload.append("services", JSON.stringify(formData.services));
    // append file last
    if (logoFile) payload.append("logo", logoFile);

    try {
      const res = await fetch("http://localhost:3001/company", {
        method: "POST",
        body: payload,
      });

      if (res.ok) {
        router.push("/dashboard");
      } else {
        const error = await res.json();
        console.error("Update failed", error);
        alert(error.message || "Failed to update company");
      }
    } catch (err) {
      console.error("Submission error:", err);
      alert("Error submitting form");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Back Button */}
      <Button asChild variant="ghost" className="mb-4">
        <Link href="/dashboard">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Dashboard
        </Link>
      </Button>

      <div>
        <h1 className="text-3xl font-bold">Edit Company Profile</h1>
        <p className="text-muted-foreground">
          Update your company information and services
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Company Logo */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Building2 className="h-5 w-5" /> Company Logo
            </CardTitle>
            <CardDescription>
              Upload your company logo (recommended size: 200x200px)
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="flex items-center gap-6">
              <Avatar className="h-24 w-24">
                <AvatarImage
                  src={logoPreview || "/placeholder.svg"}
                  alt="Company logo"
                />
                <AvatarFallback className="text-2xl">
                  {formData.name
                    .split(" ")
                    .map((n) => n[0])
                    .join("")}
                </AvatarFallback>
              </Avatar>
              <div className="space-y-2">
                <Label htmlFor="logo-upload" className="cursor-pointer">
                  <div className="flex items-center gap-2 px-4 py-2 border border-input rounded-md hover:bg-accent hover:text-accent-foreground transition-colors">
                    <Upload className="h-4 w-4" />
                    <span>Upload New Logo</span>
                  </div>
                  <Input
                    id="logo-upload"
                    type="file"
                    accept="image/*"
                    onChange={handleLogoUpload}
                    className="hidden"
                  />
                </Label>
                <p className="text-xs text-muted-foreground">
                  Supported formats: JPG, PNG, GIF (max 5MB)
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* ... rest of form unchanged ... */}

        {/* Action Buttons */}
        <Card>
          <CardContent className="pt-6">
            <div className="flex gap-4">
              <Button type="submit" disabled={isLoading} className="flex-1">
                {isLoading ? "Updating Profile..." : "Update Profile"}
              </Button>
              <Button
                type="button"
                variant="outline"
                onClick={() => router.back()}
                className="flex-1 bg-transparent"
              >
                Cancel Changes
              </Button>
            </div>
          </CardContent>
        </Card>
      </form>
    </div>
  );
}
